#search-btn-autocomplete #globalSearchIconOuter {
    width: 20px;
    margin-top: 0px
}
#search-btn-autocomplete
{
    border: none;
    background: none;
}
#search-btn-autocomplete #globalSearchIconInner {
    position: fixed;
    width: 20px;
    margin: 22px -24px 12px;
}

.ui-menu .ui-menu-item-wrapper {
    overflow: hidden;
}

.ui-menu .ui-widget .ui-widget-content .ui-autocomplete .ui-front {
    overflow: hidden;
}

#globalSearch {
    border: none;
    background-color: #e9e9e9;
    outline: none;
    width: 250px;
    padding: 6px;
}

#globalSearch:focus {
    border: none;
    background-color: #e9e9e9;
    outline: none;
    width: 240px;
    text-align: left;
    cursor: auto;
    margin-top: 14px;
}

#ui-id-1 {
    font-family: inherit;
    font-size: inherit;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgb(216, 216, 216);
    overflow: hidden;
    cursor: pointer;
    border-radius: 5px;
    width: 240px;
}

