.ms-Panel-contentInner {
    background-color: #fff;
}

*, ::after, ::before {
    box-sizing: border-box;
}

.ms-Panel-headerText {
    margin-left: 6px;
}

* {
    margin: 0;
    padding: 0;
}

.msxDropDown {
    color: inherit;
    background-size: 30px;
}

label {
    display: inline-block;
    max-width: 100%;
    margin-bottom: 5px;
    font-weight: 700;
}

.ProfileModal {
    width: 500px;
    margin-left: calc(-233px + 50%);
    height: 450px;
    background-color: #ffffff;
    padding: 10px 30px 30px 30px;
    font-family: "Segoe UI";
    border: 1px solid rgba(0, 0, 0, .2);
    border-radius: 6px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.5);
}

.form-control {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

.requiredMarker {
    color: red;
    background-color: transparent;
}

.form-group {
    margin-bottom: 15px;
}

.modalClose {
    font: bold 21px;
    color: #dcdcdc;
    float: right;
}

.help-block {
    font-size: 12px;
}

.hidden {
    display: none!important;
}

#saveProfilebtn {
    float: right;
    margin-top: 12px;
    width: 100px;
}

.commonModalBtn, .pagination>.active>a, .pagination>.active>a:focus, .pagination>.active>a:hover, .pagination>.active>span, .pagination>.active>span:focus, .pagination>.active>span:hover {
    background-color: #0078d7 !important;
    color: #fff !important;
}

.btn-primary, .btn-secondary {
    background-color: #0078D7 !important;
    color: #FFFFFF !important;
}

.btn-primary {
    color: #fff;
    background-color: #337ab7;
    border-color: #2e6da4;
}

.btn {
    display: inline-block;
    padding: 6px 12px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
}

.submit-action {
    bottom: 20px;
    position: absolute;
}

#myProfileTitle {
    font-size: 26px;
    padding: 10px 7px 6px 0px;
}

#myProfileHelpText {
    font: italic 12px "Segoe UI";
    padding-bottom: 15px;
}

.ProfileDiv {
    display: table-cell;
    vertical-align: middle;
}

#ProfilePageContainer {
    background-color: #fff;
    display: table;
    position: absolute;
    height: 100%;
    width: 100%;
}

.removeAppliedFilter, .msxLoadingDropDown, .msxDropDown {
    -webkit-appearance: none;
}

.modal-content, .form-control, .dropdown-menu, .msxDropDown, .panel-default {
    border-radius: 0px !important;
}

div {
    display: block;
}